<template>
  <div class="dashboard-page-chart">
    <div class="dashboard-page-chart__body">
      <b-row class="dashboardBody">
        <b-col md="12">
          <ACCList2
            :prop="propList"
            :title="'List Fleet Maintenance'"
            @rowClicked="rowClicked"
            @buttonDeleteClicked="doDeleteClick"
            @rowDblClicked="doDoubleClick"
            @rowLinkClick="rowLink"
            @pageSize="M_PageSize"
            @pagination="M_Pagination"
            @filter="M_Advance_Filter"
            @headTable="M_Head_Table"
            @refreshColumn="refreshColumn"
            ref="ref_list"
            @buttonViewClicked="doViewClick"
          >
            <template slot="last_millage" slot-scope="data">
              <span>{{isCurrency(data.item.last_millage, 0)}}</span>
            </template>
            <template slot="current_millage" slot-scope="data">
              <span>{{isCurrency(data.item.current_millage, 0)}}</span>
            </template>
            <template slot="usage" slot-scope="data">
              <span>{{isCurrency(data.item.usage, 0)}}</span>
            </template>
          </ACCList2>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/OP/OP_FleetMaintSch",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' ",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
      },
    };
  },
  computed: {
    ButtonStatus() {
      return this.$store.getters.getButtonStatus;
    },
    paramFromList() {
      let param = this.$store.getters.getParamPage;
      if (param == null || param == undefined) {
        this.doBack();
      } else {
        if (Object.keys(param).length < 1) {
          this.doBack();
        } else {
          return param;
        }
      }
    },
  },    
  methods: {
    rowClicked(record, index) {
      this.doViewClick(record, index);
    },
    doViewClick(record, index) {
    },
    doDoubleClick(record, index) {},
    doDeleteClick(record, index) {
      this.alertConfirmation("Are You Sure Want To Delete This Data ?").then(
        ress => {
          if (ress.value) {
            this.M_Delete(record, index);
          }
        }
      );
    },
    M_Delete(record, index) {
    },
    rowLink(url) {},
    M_PageSize() {},
    M_Pagination() {},
    M_Advance_Filter() {},
    M_Head_Table() {},
    refreshColumn() {},
    RenderData() {
      switch (this.paramFromList.indexDate) {
        case 0:
          this.propList.initialWhere += `and vmm_maintenance_fleet_sch_list.schedule_status <> 'U' 
                                          and (vmm_maintenance_fleet_sch_list.schedule_date::timestamp::date = current_date or
                                          vmm_maintenance_fleet_sch_list.queued_date::timestamp::date = current_date or
                                          vmm_maintenance_fleet_sch_list.maint_start_date::timestamp::date = current_date)`;
          break;
        case 1:
          this.propList.initialWhere += `and vmm_maintenance_fleet_sch_list.schedule_status <> 'U' 
                                          and (vmm_maintenance_fleet_sch_list.schedule_date::timestamp::date = (current_date  + interval '1 day') or
                                          vmm_maintenance_fleet_sch_list.queued_date::timestamp::date = (current_date  + interval '1 day') or
                                          vmm_maintenance_fleet_sch_list.maint_start_date::timestamp::date = (current_date  + interval '1 day'))`;
          break;
        case 2:
          this.propList.initialWhere += `and vmm_maintenance_fleet_sch_list.schedule_status <> 'U' 
                                          and (vmm_maintenance_fleet_sch_list.schedule_date::timestamp::date = (current_date  + interval '2 day') or
                                          vmm_maintenance_fleet_sch_list.queued_date::timestamp::date = (current_date  + interval '2 day') or
                                          vmm_maintenance_fleet_sch_list.maint_start_date::timestamp::date = (current_date  + interval '2 day'))`;
          break;
        default:
          break;
      }
      this.$refs.ref_list.doGetList("");
    },
  },
  mounted() {
    this.RenderData();
  },
};
</script>